import React, { useState } from 'react';
import './app.css';

// Itemの型定義
type Item = {
  id: number;
  name: string;
};

const App: React.FC = () => {
  const [items] = useState<Item[]>([
    { id: 1, name: 'アイテムA' },
    { id: 2, name: 'アイテムB' },
    { id: 3, name: 'アイテムC' }
  ]);

  const [droppedItems, setDroppedItems] = useState<Item[]>([]);

  const onDragStart = (e: React.DragEvent<HTMLDivElement>, item: Item) => {
    e.dataTransfer.setData('application/json', JSON.stringify(item));
  };

  const onDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    const itemData = e.dataTransfer.getData('application/json');
    const droppedItem: Item = JSON.parse(itemData);

    if (droppedItems.find(i => i.id === droppedItem.id)) return;

    setDroppedItems(prev => [...prev, droppedItem]);
  };

  const onDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
  };

  return (
    <div className="container">
      <div className="list">
        <h3>ドラッグするアイテム</h3>
        {items.map(item => (
          <div
            key={item.id}
            className="draggable"
            draggable
            onDragStart={(e) => onDragStart(e, item)}
          >
            {item.name}
          </div>
        ))}
      </div>

      <div
        className="dropzone"
        onDrop={onDrop}
        onDragOver={onDragOver}
      >
        <h3>ドロップエリア</h3>
        {droppedItems.map(item => (
          <div key={item.id} className="dropped">
            {item.name}
          </div>
        ))}
      </div>
    </div>
  );
};

export default App;
